  left: 0%;
  width: 45%;
}
#buildingsLogo svg{
  width:100%;
  height:100%;
}

.somos, .suolo {
  text-align: center;
  margin-top:2rem;
  -webkit-animation: outer-left 1s 1s cubic-bezier(0.5, 0, 0.1, 1) both;
          animation: outer-left 1s 1s cubic-bezier(0.5, 0, 0.1, 1) both;
}

.somos-inner {
  font-size: 4vw;
  -webkit-animation: inner-left 1s 1s ease both, text-clip 1s 0s cubic-bezier(0.5, 0, 0.1, 1) both,change-spacing-low 1s 2s ease forwards;
          animation: inner-left 1s 1s ease both, text-clip 1s 0s cubic-bezier(0.5, 0, 0.1, 1) both,change-spacing-low 1s 2s ease forwards;
}

.suolo-inner {
  font-size: 12vw;
  line-height: .9;
  -webkit-animation: text-clip 1s 0s cubic-bezier(0.5, 0, 0.1, 1) both,change-spacing-high 1s 2s ease forwards;
          animation: text-clip 1s 0s cubic-bezier(0.5, 0, 0.1, 1) both,change-spacing-high 1s 2s ease forwards;
}

.carouselHolder {
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  z-index:1;
  -webkit-animation: carousel-in 1s cubic-bezier(0.5, 0, 0.1, 1) 2s backwards;
          animation: carousel-in 1s cubic-bezier(0.5, 0, 0.1, 1) 2s backwards;
}
#firstSectionCarousel img{
  height:100%;
  width:auto;
  opacity:.8;
}

body:active * {
  -webkit-animation: none !important;
          animation: none !important;
}


@-webkit-keyframes outer-left {
  from {
    -webkit-transform: translate(20%,-50%);
            transform: translate(20%,-50%);
  }
  to {
    left:0%;
    -webkit-transform: translate(0%,-50%);
            transform: translate(0%,-50%);
  }
}
@keyframes outer-left {
  from {
    -webkit-transform: translate(20%,-50%);
            transform: translate(20%,-50%);
  }
  to {
    left:0%;
    -webkit-transform: translate(0%,-50%);
            transform: translate(0%,-50%);
  }
}

@-webkit-keyframes text-clip {
  from {
    -webkit-clip-path: polygon(0% 100%, 100% 100%, 100% 100%, 0% 100%);
            clip-path: polygon(0% 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  to {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
@keyframes text-clip {
  from {
    -webkit-clip-path: polygon(0% 100%, 100% 100%, 100% 100%, 0% 100%);
            clip-path: polygon(0% 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  to {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

@-webkit-keyframes inner-left {
  from {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  to {
    -webkit-transform: none;
            transform: none;
  }
}
@keyframes inner-left {
  from {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  to {
    -webkit-transform: none;
            transform: none;
  }
}
@-webkit-keyframes carousel-in{
  from {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }
  to {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
@keyframes carousel-in {
  from {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }
  to {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
@-webkit-keyframes change-spacing-high{
  to{letter-spacing:2vw;
    text-shadow: 3px 3px 10px #4d4b4b;
  }
}
@keyframes change-spacing-high{
