.slidingUpHolder{
  color: rgb(0, 0, 0);
  font-family: "Titillium Web", sans-serif;
}

.text-top {
  position: absolute;
  top: 0;
  width: 100%;
  height: 20%;
  overflow: hidden;
}
.text-top-inner {
  position: absolute;
  bottom: 0;
  width:100%;
  -webkit-transform: translate(0, 100%);
          transform: translate(0, 100%);
  padding: .5vmin 0;
  font-size: 5vw;
  -webkit-animation: showTopText .6s .5s ease forwards;
          animation: showTopText .6s .5s ease forwards;
  opacity:0;
  text-transform: uppercase;
}
.reduced-font-size{
  font-size: 4vw !important;
}
@-webkit-keyframes showTopText {
  0% { -webkit-transform: translate(0, 100%); transform: translate(0, 100%); }
  100% { -webkit-transform: translate(0, 10%); transform: translate(0, 10%); opacity:1; }
}
@keyframes showTopText {
  0% { -webkit-transform: translate(0, 100%); transform: translate(0, 100%); }
  100% { -webkit-transform: translate(0, 10%); transform: translate(0, 10%); opacity:1; }
}
.text-top-inner span {
  display: block;
  text-align: left;
  padding:0 2%;
  letter-spacing: .3rem;
}
.text-bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 75%;
  overflow: hidden;
}
.text-bottom-inner {
  position: absolute;
  top: 0;
  width:100%;
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
  text-align: left;
  font-size: 1vw;
  padding: 1vw 1vw;
  opacity:0;
  -webkit-animation: showBottomText .8s ease forwards .8s;
          animation: showBottomText .8s ease forwards .8s;
}
@-webkit-keyframes showBottomText {
  0% { -webkit-transform: translate3d(0, -100%, 0); transform: translate3d(0, -100%, 0); }
  100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);opacity:1 }
}
@keyframes showBottomText {
  0% { -webkit-transform: translate3d(0, -100%, 0); transform: translate3d(0, -100%, 0); }
  100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);opacity:1 }
}
