.rankingSectionHolder{
    background-color: #dddddd;
    display: flex;
    justify-content: space-evenly;
    height:30vw;
    width:100%;
    font-family: 'Cabin Condensed', sans-serif;
}

.rankingEntry{
    width:30%;
    height:100%;
    font-size: 1.5vw;
    position:relative;
}
.rankingText{
    position:absolute;
    top:50%;
    left:50%;
    width:90%;
    height:80%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.rankingNumber{
    position:absolute;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    line-height: 1.4;
    font-size:7vw;
    -webkit-animation:number-slide-up 1s 0s ease forwards;
            animation:number-slide-up 1s 0s ease forwards;
    z-index:2;
}
@-webkit-keyframes number-slide-up {
    to {top:30%;}
}
@keyframes number-slide-up {
    to {top:30%;}
}
.rankingNumber::before{
    content:"";
    position:absolute;
    top:-5%;
    left:50%;
    height:100%;
    width:0%;
    -webkit-transform: translate(-50%,0);
            transform: translate(-50%,0);
    border-bottom:3px solid black;
    -webkit-animation:elongate-btm-border .5s 1s ease forwards;
            animation:elongate-btm-border .5s 1s ease forwards;
}
@-webkit-keyframes elongate-btm-border {
    to {width:50%}
}
@keyframes elongate-btm-border {
    to {width:50%}
}

.rankingBody{
    position:absolute;
    width:100%;
    top:0;
    -webkit-animation: text-body-slide-down 1s 0s ease forwards;
            animation: text-body-slide-down 1s 0s ease forwards;
    z-index: 0;
}
@-webkit-keyframes text-body-slide-down {
    to{top:50%;}
}
@keyframes text-body-slide-down {
    to{top:50%;}
}
.rankingBodyCover{
    position:absolute;
    top:0;
    width:100%;
    height:50%;
    z-index:1;
    background-color: #dddddd;
}
.rankingBody div{
    display:block;
    text-align: center;
    line-height: 1.4;
}
.boldText{
    font-weight:700;
    font-size:1.8vw;

}
.italicsText{
    font-style:italic;
    font-size:1.8vw;
}

